<template>
  <div class="container-fluid">
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../assets/img/loading.svg" width="100">
    </loading>
    <div class="row">
      <div class="col-lg-3 col-md-4 control">
        <img src="../assets/img/bg/left-menu.png" class="left-menu-bg-img" alt="玩音樂">
        <div class="con">
          <h2>註冊會員</h2>
          <hr>
          <button class="btn btn-outline-light" type="button" onclick="window.history.back();"><i class="fas fa-arrow-left"></i> 返回上頁 </button>						
        </div>			
      </div>
      <div class="col-lg-9 col-md-8 signup">
        <div class="right-content">
          <h3>Meason會員註冊</h3>
          <div class="form">
            <form @submit.prevent="createUser()">
              <div class="row">
                <div class="col-lg-12">
                  <img src="../assets/img/bg/signup-banner.jpg" class="mb-4 mb-sm-3"
                    width="100%" alt="讓玩音樂，變得很簡單">
                </div>
                <div class="col-lg-8">
                  <div class="form-group">
                    <label for="nickname"><i class="fas fa-music"></i>暱稱</label>
                    <input type="text" class="form-control" placeholder="請輸入您的暱稱"
                      id="nickname" name="nickname" :class="{'is-invalid': errors.has('nickname')}"
                      data-vv-as="暱稱"
                      v-model="formData.displayName" v-validate="'required|max:30'">
                  </div>
                </div>
                <div class="col-lg-4 con-word">
                  <p class="text-danger" v-if="errors.has('nickname')">
                    {{ errors.first('nickname') }}
                  </p>
                </div>
                <div class="col-lg-8">
                  <div class="form-group">
                    <label for="email"><i class="fas fa-music"></i>電子信箱</label>
                    <input type="email" class="form-control" placeholder="請輸入您的電子信箱"
                      id="email" name="email" :class="{'is-invalid': errors.has('email')}"
                      data-vv-as="電子信箱"
                      v-model="formData.email" v-validate="'required|email'">
                  </div>
                </div>
                <div class="col-lg-4 con-word">
                  <p class="text-danger" v-if="errors.has('email')">
                    {{ errors.first('email') }}
                  </p>
                  <p class="text-info" v-else>*請填入正確信箱避免漏接重要訊息</p>
                </div>
                <div class="col-lg-8">
                  <div class="form-group">
                    <label for="mypassword"><i class="fas fa-music"></i>輸入密碼</label>
                    <input type="password" class="form-control" placeholder="請填寫您的密碼"
                      id="mypassword" name="mypassword" :class="{'is-invalid': errors.has('mypassword')}"
                      data-vv-as="密碼"
                      v-model="formData.password" v-validate="'required|min:8'">
                  </div>
                </div>
                <div class="col-lg-4 con-word">
                  <p class="text-danger" v-if="errors.has('mypassword')">
                    {{ errors.first('mypassword') }}
                  </p>
                </div>
                <div class="col-lg-8">
                  <div class="form-group">
                    <label for="ensurepassword"><i class="fas fa-music"></i>密碼確認</label>
                    <input type="password" class="form-control" placeholder="請再次確認您的密碼"
                      id="ensurepassword" name="ensurepassword" :class="{'is-invalid': errors.has('ensurepassword')}"
                      data-vv-as="密碼確認"
                      v-validate="{ required : true, confirmed: formData.password}">
                  </div>
                </div>
                <div class="col-lg-4 con-word">
                  <p class="text-danger" v-if="errors.has('ensurepassword')">
                    請填寫正確密碼
                  </p>
                </div>
                <div class="col-lg-12 d-flex mt-3">
                  <button class="btn btn-primary">立即註冊</button>
                  <small class="ml-3">註冊 Meason ID，表示您已同意 Meason 的<a href="privacy-policy.html" target="_blank">隱私權保護政策</a>，包括 cookies 的使用、其他人將能夠在本站上取用您的相關公開資訊。</small>
                </div>
              </div>
              </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  name: 'sinup',
  data() {
    return {
      isLoading: false,
      formData: {
        displayName: '',
        email: '',
        password: '',
      },
      errorMsg: '',
    }
  },
  methods: {
    createUser() {
      const vm = this;
      this.$validator.validate().then((result) => {
        if (result) {
          // do stuff if not valid.
          vm.isLoading = true;
          firebase.auth().createUserWithEmailAndPassword(vm.formData.email, vm.formData.password)
          .then((user) => {
            vm.createUserApi(user.user.uid)
          })
          .catch((error) => {
            alert(error.message);
            vm.$router.push('/');
          })
        } else {
          console.log('欄位不完整')
        }
      });
    },
    createUserApi(uid) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/fbLogin`;
      const newFormData = {
        email: vm.formData.email,
        displayName: vm.formData.displayName,
        photoURL : 'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/user_photo%2Fundefine.jpg?alt=media&token=0efb2eb9-a619-4af9-8b53-e5f3163846db',
        echoin: 0,
        uid: uid,
      }
      this.$http.post(url, {"uid": uid, "user": newFormData}).then((response) => {
        vm.$router.push('/');
        vm.isLoading = false;
      })
      .catch(function(error){
        console.error("寫入使用者資訊錯誤",error);
      });
    },
  },
}
</script>

